* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #0e80b0;
}

.btn {
  background-color: #fff;
  color: #555;
  text-transform: uppercase;
  margin: 15px;
  padding: 5px;
  border: 2px solid #080;
  border-radius: 8px;
  box-shadow: 4px 4px 12px #080;
  font-size: 50px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  position: relative;
  top: 0;
}

.btn:hover {
  background-color: #180;
  color: #fff;
  top: 4px;
  box-shadow: 6px 6px 14px #080;
}

.btn:active {
  top: 8px;
  box-shadow: 6px 6px 12px #080;
}

.disabled .btn {
  cursor: default;
  background-color: #555;
  color: #fff;
}

.disabled .btn:hover {
  top: 0;

  color: #fff;
  box-shadow: 6px 6px 14px #080;
}

.btn.disabled {
  cursor: default;
}

.btn.disabled:hover {
  top: 0;

  color: #fff;
  box-shadow: 6px 6px 14px #080;
}

.game {
  display: flex;
  flex-wrap: wrap;
}

.game-info {
  margin: 15px;
  color: #fff;
}

.board > div {
  display: flex;
}
